@import "../../../assets/styles/mixin.scss";
@import "../../../assets/styles/variable.scss";
@keyframes menuShow {
  from {
    width: 0;
  }
  to {
    width: 47.05vw;
  }
}

@keyframes menuHide {
  from {
    width: 47.05vw;
  }
  to {
    width: 0;
  }
}
@include desktop2k() {
  @keyframes menuShow {
    from {
      width: 0;
    }
    to {
      width: 47.5vw;
    }
  }
  @keyframes menuHide {
    from {
      width: 47.5vw;
    }
    to {
      width: 0;
    }
  }
}
.com-slider {
  #checkbox3 {
    display: none;
  }
  .MuiSelect-icon {
    color: white;
  }
  /* HAMBURGER 3 */
  .hamburger {
    margin: 0 auto;
    width: 30px;
    height: 30px;
    position: relative;
    overflow: hidden;
    transform: scale(0.8);
    cursor: pointer;
    @include desktop2k {
      transform: scale(2);
    }
    .bar {
      position: absolute;
      padding: 0;
      width: 30px;
      height: 4px;
      background-color: $hong-color;
      display: block;
      border-radius: 5px;
      transition: all 0.4s ease-in-out;
    }
  }
  .bar1 {
    top: 0;
  }
  .bar2 {
    top: 13px;
  }
  .bar3 {
    top: 13px;
    right: 0;
  }
  .bar4 {
    bottom: 0;
  }
  .hamburger3 {
    .bar1 {
      transform-origin: 5%;
    }
    .bar4 {
      transform-origin: 5%;
    }
  }
  .checkbox3 {
    &:checked {
      & + label {
        > .hamburger3 {
          > .bar1 {
            transform: rotate(45deg);
            height: 4px;
            width: 44px;
          }
          > .bar3 {
            transform: rotate(45deg);
            height: 4px;
            background-color: transparent;
          }
          > .bar2 {
            transform: rotate(-45deg);
            height: 4px;
            background-color: transparent;
          }
          > .bar4 {
            transform: rotate(-45deg);
            height: 4px;
            width: 44px;
          }
        }
      }
    }
  }
}

@include mobile {
  .com-slider {
    position: fixed;
    top: 0;
    left: 0;
    font-family: "Montserrat", sans-serif;
    z-index: 9998;
    .slider-left {
      left: 0;
      top: 0;
      width: 100vw;
      height: $sliderheight;
      display: flex;
      justify-content: space-between;
      align-items: center;
      background-color: black;
      padding: 0.5rem $pdMobile;
      transition: 0.4s background-color cubic-bezier(0.075, 0.82, 0.165, 1);

      > img {
        padding-left: 0.5rem;
      }
      .logo-black {
        display: none;
      }
      .ul-lang {
        display: none;
        &::after {
          border-color: $hong-color !important;
        }
      }

      .block-control {
        position: relative;
        width: 50px;
        height: 50px;
        margin-right: -0.5rem;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        background-color: black;
        transform: scale(0.85);
        .menu-show,
        .menu-hide {
          position: absolute;
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
          margin: auto;
          cursor: pointer;
          transition: 0.3s all ease-in;
          background-color: black;
          border-radius: 50%;
          &:hover {
            transform: scale(1.05);
          }
        }

        .menu-hide {
          opacity: 0;
          animation-delay: 0.2s;
          color: $hong-color;
        }
        .delay-show {
          animation-delay: 0.3s;
        }
      }
    }
    .slider-right {
      position: fixed;
      left: 0;
      top: 9.95vh;
      width: 100vw;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      max-height: 0;
      overflow: hidden;
      background-color: $bg-color;
      transition: all 0.35s cubic-bezier(0.77, 0, 0.175, 1);

      & > div.wrapper {
        opacity: 0;
        overflow: auto;
        height: 90vh;
        .label {
          display: none;
        }
        .list-menu {
          flex: 1;
          ul {
            font-size: 3.5rem;
            font-weight: 800;
            line-height: 1.5;
            li {
              width: fit-content;
              cursor: pointer;
              &.showing {
                .li-arrow {
                  transform: rotate(-180deg);
                }

                & + .list-subcate {
                  width: fit-content;
                  max-height: 300px;
                  opacity: 1;
                  transition: all 0.35s ease-in-out;
                  cursor: pointer;
                }
              }
            }
            .li-arrow {
              vertical-align: middle;
              font-size: 3rem;
              margin-left: 1rem;
              transition: all 0.4s ease;
            }
            .list-subcate {
              font-size: 1.5rem;
              opacity: 0;
              overflow: hidden;
              max-height: 0;
              transition: all 0.55s linear;
            }
          }
        }

        & > .block-contact {
          display: block;
          max-width: 250px;
          font-size: 13px;
          margin-top: 2rem;
          .title {
            font-weight: 600;
            margin-bottom: 1rem;
          }
          .content.link {
            display: none;
          }
        }
        & > .block-contact-link {
          display: flex;
          justify-content: space-between;
          width: 100%;
          .content {
            font-family: "Lato", sans-serif;
            &.link {
              display: inline-block;
              img {
                margin-right: 1rem;
                cursor: pointer;
              }
            }
          }
          .ul-lang {
            float: right;
            &::after {
              border-color: $hong-color !important;
            }
          }
        }
      }

      &.showing {
        & > div.wrapper {
          display: flex;
          flex-direction: column;
          justify-content: unset;
          align-items: flex-start;
          animation: fadeIn 0.75s ease forwards;
          animation-delay: 0.5s;
          width: 100%;
          padding: $pdMobile;
        }
      }
    }
    &.opened {
      .MuiSelect-icon {
        color: black;
      }
      .slider-left {
        background-color: $bg-color;
        transition: 0.2s background-color cubic-bezier(0.075, 0.82, 0.165, 1);
        .logo-white {
          display: none;
        }
        .logo-black {
          display: block;
        }
        .ul-lang {
          color: black;
        }
      }
      .slider-right {
        max-height: 90vh;
        transition: all 0.35s cubic-bezier(0.77, 0, 0.175, 1);
      }
    }
  }

  .float-contact {
    position: fixed;
    z-index: 9999;
    right: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: rgba(0, 0, 0, 0.4);
    .content {
      font-family: "Lato", sans-serif;
      display: flex;
      flex-flow: column nowrap;
      border-radius: 4px;
      color: #ff3050;
      img {
        cursor: pointer;
        @include desktop2k {
          width: 50px;
          height: 50px;
        }
      }
      .contact {
        position: relative;
        display: block;
        padding: 6px 4px 4px 4px;
        @include desktop2k {
          padding: 1rem;
        }
        svg {
          width: 27px;
          height: 27px;
          @include desktop2k {
            width: 50px;
            height: 50px;
          }
        }
      }
      .linkedin {
        position: relative;
        padding: 4px 4px 4px 4px;
        border-top: 1px solid rgba($color: #000, $alpha: 0.3);
        border-bottom: 1px solid rgba($color: #000, $alpha: 0.3);
        @include desktop2k {
          padding: 1rem;
        }
      }
      .vimeo {
        position: relative;
        padding: 4px 4px 6px 4px;
        @include desktop2k {
          padding: 1rem;
        }
      }
      .contact:hover,
      .linkedin:hover,
      .vimeo:hover {
        .hint {
          width: 90px;
        }
      }
      .hint {
        position: absolute;
        overflow: hidden;
        right: 100%;
        top: 0%;
        height: 100%;
        width: 0px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: rgba(0, 0, 0, 0.4);
        transition: width 0.3s ease-out;
        white-space: nowrap;
        color: white;
      }
    }
  }
}

@include desktop {
  .com-slider {
    .slider-left {
      .block-control {
        transform: unset;
        @include desktop2k {
          width: 120px;
          height: 120px;
          transform: scale(0.8);
        }
      }
      left: 0;
      top: 0;
      width: 5.075vw;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      height: 100vh;
      max-height: unset;
      .logo-black {
        display: none;
      }
      .ul-lang {
        display: flex;
        min-width: 70px;
        color: $bg-color;
        font-size: 0.9rem;
      }
      img {
        @include desktop2k {
          margin-top: 50px;
          transform: scale(2);
        }
      }
    }
    .slider-right {
      left: 4.95vw;
      top: 0;
      width: unset;
      max-height: unset;
      animation-delay: 0.2s;
      height: 100vh;
      & > div.wrapper {
        width: 0;

        .label {
          font-weight: 600;
          margin-top: 3rem;
        }
        .list-menu {
          ul {
            font-size: 3rem;
            line-height: 1.5;
            margin-left: 3vw;
            @include desktop2k {
              font-size: 4rem;
              line-height: 1.25;
            }
            .li-arrow {
              font-size: 3.5rem;
            }
            .list-subcate {
              font-size: 2rem;
            }
            .block-contact-ul {
              display: none;
            }
          }
        }

        & > .block-contact {
          display: flex;
          position: absolute;
          bottom: 0;
          left: 8vw;
          max-width: 250px;
          font-size: 13px;
          margin: 0;
          @include desktop2k {
            max-width: 600px;
            font-size: 1.5rem;
            bottom: 70px;
          }
          .title {
            margin-right: 3rem;
            margin-bottom: 0.5rem;
            font-weight: 600;
          }
          .content {
            margin-bottom: 0.5rem;
            &.link {
              display: block;
              img {
                margin-right: 1rem;
                cursor: pointer;
                @include desktop2k {
                  width: 80px;
                  margin-right: 50px;
                }
              }
            }
          }
        }
        & > .block-contact-link {
          display: none;
        }
      }

      &.active {
        animation: menuHide 0.5s cubic-bezier(0.165, 0.84, 0.44, 1) forwards;
      }
      &.showing {
        & > div.wrapper {
          padding: 10vh 5vw;
          padding-bottom: 0;
        }
      }
    }
    &.opened {
      .slider-left {
        background-color: $bg-color;
        transition: 0.2s background-color cubic-bezier(0.77, 0, 0.175, 1);
        .logo-white {
          display: none;
        }
        .logo-black {
          display: block;
        }
        .ul-lang {
          color: black;
        }
      }
      .slider-right {
        max-height: unset;
        animation: menuShow 0.5s cubic-bezier(0.895, 0.03, 0.685, 0.22) forwards;
      }
    }
  }
}
