@import "assets/styles/mixin.scss";
@import "assets/styles/variable.scss";
.com-form-download input[type="checkbox"] {
  display: unset;
  opacity: unset;
  position: unset;
  -webkit-appearance: checkbox;
  width: unset;
  @include desktop2k {
    width: 25px;
    height: 25px;
  }
}
.com-form-download {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  max-width: 1040px;
  padding: $pdMobile;
  background-color: white;
  opacity: 0;
  visibility: hidden;
  transition: 0.25s all ease;
  overflow: auto;
  z-index: 9999;
  @include desktop() {
    height: unset;
    top: 20%;
    right: 0;
    margin: auto;
    padding: 5rem 8rem;
    font-size: 1.1rem;
    box-shadow: 0px 3px 15px 0px rgba(0, 0, 0, 0.45);
    @include desktop2k {
      max-width: 1600px;
    }
  }

  input[type="number"]::-webkit-outer-spin-button,
  input[type="number"]::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  input[type="number"] {
    -moz-appearance: textfield;
  }

  & > img {
    position: absolute;
    right: 0.5rem;
    top: 0.5rem;
    transform: scale(0.8);
    cursor: pointer;
    @include desktop() {
      right: 1.5rem;
      top: 1.5rem;
      transform: unset;
      @include desktop2k {
        right: 2rem;
        top: 2rem;
        transform: scale(1.5);
      }
    }
  }

  input,
  select,
  textarea {
    display: block;
    width: 100%;
    border: 1px solid #6161619d;
    border-radius: 6px;
    padding: 0.5rem 0.75rem;
    -webkit-appearance: none;
    background-color: transparent;
    outline: none;
    transition: 0.4s;
    font-size: 1rem;
    font-family: "Lato", sans-serif;
    @include desktop2k() {
      font-size: 1.25rem;
    }
  }

  .title {
    @include title();
    font-size: 2rem;
    margin-bottom: 1.5rem;
    @include desktop() {
      font-size: 3rem;
    }
  }
  .sub-tit {
    margin-bottom: 2rem;
  }
  form {
    @include desktop() {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-gap: 0 2rem;
    }
    .item {
      position: relative;
      margin-bottom: 1rem;
      &.mess {
        grid-column: 1/3;
      }
      &.checkbox {
        grid-column: 1/3;
        label,
        .recaptcha {
          font-size: 1rem;
          font-weight: 400;
          margin-left: 10px;
          margin-top: 0.75rem;
          cursor: pointer;
          &.mg-top0 {
            margin-left: 0;
            cursor: default;
          }
          a {
            font-weight: 700;
            text-decoration: underline;
            margin: 0 4px;
          }
          @include desktop2k {
            font-size: 1.25rem;
          }
        }
      }
      .dropdown {
        margin-top: 0.3rem;
        &::before {
          top: 43%;
          transform: translateY(-50%) scale(1.35);
        }
      }
      .label {
        @include title();
        text-transform: unset;
        font-size: 1.15rem;
        @include desktop2k {
          font-size: 1.5rem;
        }
      }
      .errCheck {
        position: absolute;
        color: #ff7474;
        font-weight: 700;
        font-size: 1rem;
      }
      .error {
        position: absolute;
        right: 0;
        top: -1rem;
        background-color: #ff7474;
        border-radius: 5px;
        color: white;
        padding: 0.75rem 1rem;
        z-index: 5;
        font-size: 0.9rem;

        &:after {
          content: " ";
          position: absolute;
          bottom: -15%;
          right: 10px;
          width: 0;
          height: 0;
          border-left: 10px solid transparent;
          border-right: 10px solid transparent;
          border-top: 10px solid #ff7474;
        }
        & + input,
        & + textarea {
          border-color: #ff7474;
        }
      }
      > input,
      > select,
      > textarea {
        margin-top: 0.35rem;
        @include desktop2k {
          margin-top: 0.5rem;
        }
        &::placeholder {
          color: gray;
        }
      }
      > svg {
        position: absolute;
        right: 0.5rem;
        top: 50%;
      }
      @include desktop() {
        textarea {
          min-height: 120px;
        }
      }
    }
  }
  .btn-submit {
    @include btn();
    width: 100%;
    outline: none;
    border: none;
    margin-top: 1.5rem;
    
    @include desktop{
      grid-column: 1 / span 2;
      max-width: 50%;
      margin: 3rem auto;
      margin-bottom: 0;
    }
    
    transition: 0.4s background-color ease;
    &:disabled {
      background-color: darkgrey;
    }
  }
}
