@import "./mixin.scss";
@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700;800&display=swap");
//overide material
.MuiSelect-select.MuiSelect-select {
  padding-right: 0 !important;
}
.MuiDialog-paper {
  margin: unset !important;
}
.MuiPopover-root {
  z-index: 9999 !important;
}

a {
  color: unset;
  text-decoration: unset;
}
ul {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
.opa-0 {
  opacity: 0 !important;
}
.opa-1 {
  opacity: 1;
}
.dp-none {
  display: none !important;
}
.mg-0 {
  margin: 0 !important;
}
.pd-0 {
  padding: 0 !important;
}
.cl-hong {
  color: $hong-color;
}
.visible {
  visibility: visible !important;
}
.blank {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.75);
  z-index: 9998;
}
.hidden {
  visibility: hidden !important;
}
// .title{
//   @include title();
// }
//overide Sweet alert
.swal2-container {
  z-index: 9999 !important;
}
.lazaImg-abs {
  position: absolute;
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.swal2-header {
  font-family: "Lato", sans-serif;

  .swal2-title {
    font-size: 1.55rem;
    color: black;
    @include title();
  }
}
#swal2-content {
  color: black !important;
  font-size: 1.25rem;
  font-weight: 500 !important;
}
.swal2-actions {
  .swal2-confirm {
    border-radius: 33px;
    background-color: #ff3050 !important;
    @include btn();
    min-width: 120px;
  }
}
//Config dots slider
.com-value-dots {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 1.5px solid $hong-color;
  cursor: pointer;
  transition: 0.4s background-color ease-in-out;
  &.white {
    border: 1.5px solid white;
  }
  @include desktop {
    width: 18px;
    height: 18px;
  }
}
// Overide cookie style
#cookieNotification {
  .gl-co-capt {
    a {
      font-weight: 700;
      text-decoration: underline;
    }
  }
}
.slick-active > a > .com-value-dots {
  background-color: $hong-color;
  &.white {
    background-color: white;
  }
}
// Cookie override
#cookieNotification {
  .gl-co-capt {
    a {
      font-weight: 700;
      text-decoration: underline;
    }
  }
}
@keyframes noise-anim {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      clip: rect(random(100) + px, 9999px, random(100) + px, 0);
    }
  }
}

@keyframes noise-anim-2 {
  $steps: 20;
  @for $i from 0 through $steps {
    #{percentage($i*(1/$steps))} {
      clip: rect(random(100) + px, 9999px, random(100) + px, 0);
    }
  }
}
